/* src/components/Navbar.css */

/* Common navbar styles */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #333;
  padding: 1rem;
  color: white;
  z-index: 1000;
}

.navbar-title {
  font-size: 1.5rem;
}

.navbar-links {
  list-style: none;
  display: flex;
  gap: 1rem;
  margin: 0;
}

.navbar-links a {
  color: white;
  text-decoration: none;
}

.navbar-links a:hover {
  text-decoration: underline;
}

/* Hide the mobile menu icon on PC */
.menu-icon {
  display: none;
  font-size: 1.5rem;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}

/* Mobile-specific styles */
@media (max-width: 768px) {
  /* Show menu icon, hide desktop menu */
  .menu-icon {
    display: block;
  }

  /* Hide links by default on mobile */
  .navbar-links {
    display: none;
    position: absolute;
    top: 60px;
    left: 0;
    width: 100%;
    background-color: #333;
    flex-direction: column;
    gap: 0.5rem;
    padding: 1rem;
  }

  /* Show links when mobile menu is open */
  .navbar-links.open {
    display: flex;
  }

  .navbar-title {
    font-size: 1.25rem;
  }
}
